import Vue from 'vue'
import VueRouter from 'vue-router'

import WelcomePage from './components/welcome/welcome.vue'
import StaffPage from './components/auth/staffPage.vue'
import PatientPage from './components/auth/patientPage.vue'


Vue.use(VueRouter)

const routes = [
   { 
    path: '/', 
    component: WelcomePage
   },
   { 
    path: '/patientPage', 
    component: PatientPage,
   },
   { 
    path: '/staffPage', 
    component: StaffPage,
   },    

]

export default new VueRouter({mode: 'history', routes})



